import {
  BaseColumn,
  ColumnConfig,
  getBasicInputArrayFormFieldConfig,
  gettext
} from '@c8y/ngx-components';

export class SystemIdDeviceGridColumn extends BaseColumn {
  constructor(initialColumnConfig?: ColumnConfig) {
    super(initialColumnConfig);

    this.path = 'id';
    this.name = 'systemId';
    this.header = gettext('System ID');

    this.filterable = true;
    this.filteringConfig = {
      fields: getBasicInputArrayFormFieldConfig({
        key: 'ids',
        label: gettext('Show items with system ID'),
        addText: gettext('Add next`id`'),
        tooltip: gettext('Use * as a wildcard character'),
        placeholder: '10300'
      }),
      getFilter: (model: any): any => {
        const filter: any = {};
        if (model.ids.length) {
          filter[this.path] = { __in: model.ids };
        }
        return filter;
      }
    };

    this.sortable = true;
    this.sortingConfig = {
      pathSortingConfigs: [{ path: this.path }]
    };
  }
}
