import {
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import { IManagedObject } from '@c8y/client';
import {
  ActionControl,
  BuiltInActionType,
  BulkActionControl,
  Column,
  DataGridComponent,
  DataSourceModifier,
  FilteringActionType,
  FilteringModifier,
  gettext,
  HeaderActionControl,
  LoadMoreMode,
  Pagination,
  ProductExperienceEvent,
  ProductExperienceEventSource,
  PRODUCT_EXPERIENCE_EVENT_SOURCE,
  Row,
  ServerSideDataCallback,
  ServerSideDataResult,
  toObservable
} from '@c8y/ngx-components';
import { groupBy } from 'lodash-es';
import { EMPTY, from, Subject } from 'rxjs';
import { catchError, map, switchMap, takeUntil } from 'rxjs/operators';
import { DeviceGridExtensionService } from './device-grid-extension.service';
import { DataCallback, DeviceGridActionHook, FilterConfig } from './device-grid.model';
import { DeviceGridService } from './device-grid.service';

@Component({
  selector: 'c8y-device-grid',
  templateUrl: './device-grid.component.html',
  providers: [
    {
      provide: PRODUCT_EXPERIENCE_EVENT_SOURCE,
      useExisting: forwardRef(() => DeviceGridComponent)
    }
  ]
})
export class DeviceGridComponent implements OnInit, OnDestroy, ProductExperienceEventSource {
  /** Optional callback function that allows to modify server side data result before it's rendered. */
  @Input() dataCallback: DataCallback;
  /** Takes an event emitter. When an event is emitted, the grid will be reloaded. */
  @Input() refresh: EventEmitter<void> = new EventEmitter();
  /** The title for the data grid, it's displayed in the grid's header. */
  @Input() title: string = gettext('Devices');
  /** The label for load more button. */
  @Input() loadMoreItemsLabel = gettext('Load more devices');
  /** The label for loading indicator. */
  @Input() loadingItemsLabel: string = gettext('Loading devicesâ€¦');
  /**
   * @deprecated
   *
   * Internal use only: used to define user preferences key under which 'All devices' column config is stored.
   */
  @Input() legacyConfigKey: string;
  /**
   * @deprecated
   *
   * Internal use only: used to define user preferences key under which 'All devices' filter/sorting config is stored.
   */
  @Input() legacyFilterKey: string;
  /** The list of columns to be displayed in the grid. If not given, it defaults to standard columns. */
  @Input('columns') columns: Column[] = this.deviceGridService.getDefaultColumns();
  /** Pagination settings, e.g. allows for setting current page or page size. If not given, defaults to standard settings. */
  @Input('pagination') set _pagination(value: Pagination) {
    if (value) {
      this.pagination = value;
    }
  }
  infiniteScroll: LoadMoreMode;
  /** Sets load more mode. */
  @Input('infiniteScroll') set _infiniteScroll(infiniteScroll: LoadMoreMode) {
    this.infiniteScroll = infiniteScroll;
    if (infiniteScroll) {
      this.pagination = this.deviceGridService.getInfiniteScrollPagination();
    }
  }
  /** Sets action controls (actions available for individual items). If not given, it defaults to standard actions. */
  @Input('actionControls') set _actionControls(value: ActionControl[]) {
    if (value) {
      this.actionControls = value;
    } else {
      this.actionControls = this.deviceGridService.getDefaultActionControls();
    }
  }
  /** Determines whether items can be selected by clicking a checkbox in the first column. */
  @Input() selectable = false;
  /** Restricts selection to a single row only. Selection column displays radio button instead of checkboxes */
  @Input() singleSelection = false;
  /** Sets the base query which is appended to the request for data. */
  @Input() baseQuery: object = {};
  /** Sets bulk action controls (actions available for items selected by user). If not given, it defaults to standard bulk actions. */
  @Input('bulkActionControls') set _bulkActionControls(value: BulkActionControl[]) {
    if (value) {
      this.bulkActionControls = value;
    } else {
      this.bulkActionControls = this.deviceGridService.getDefaultBulkActionControls();
    }
  }
  /** Sets header action controls (actions available from the grid header). If not given, it defaults to empty list of actions. */
  @Input('headerActionControls') set _headerActionControls(value: HeaderActionControl[]) {
    if (value) {
      this.headerActionControls = value;
    } else {
      this.headerActionControls = this.deviceGridService.getDefaultHeaderActionControls();
    }
  }
  @Input() childDeviceGrid = false;
  @Input() parentDeviceId: string;
  /** Sets the withChildren query which is appended to the request. */
  @Input() withChildren = false;
  /**
   * Enables the search for devices where any device propery is matched agains the search term.
   * Enabled by default. This input does not take effect if the <code>childDeviceGrid</code> input is set to <code>true</code>
   */
  @Input() showSearch = true;
  /**
   * Sets the class name used for active rows (last clicked).
   * Set empty string to disable appending active class to grid rows.
   */
  @Input()
  activeClassName = 'active';
  /** Emits an event when columns configuration changes. */
  @Output() onColumnsChange: EventEmitter<Column[]> = new EventEmitter<Column[]>();
  @Output() onFilterChange: EventEmitter<FilterConfig[]> = new EventEmitter<FilterConfig[]>();
  @Output() onDeviceQueryStringChange: EventEmitter<string> = new EventEmitter<string>();
  /** Emits an event when items selection changes. The array contains ids of selected items. */
  @Output() itemsSelect = new EventEmitter<string[]>();

  actionControls: ActionControl[] = [];
  appliedFilters: FilterConfig[] = [];
  pagination: Pagination = this.deviceGridService.getDefaultPagination();
  bulkActionControls: BulkActionControl[] = this.deviceGridService.getDefaultBulkActionControls();
  headerActionControls: HeaderActionControl[] =
    this.deviceGridService.getDefaultHeaderActionControls();
  serverSideDataCallback: ServerSideDataCallback;

  @ViewChild(DataGridComponent, { static: true })
  dataGrid: DataGridComponent;
  productExperienceEvent: ProductExperienceEvent;

  private destroyed$: Subject<void> = new Subject();

  constructor(
    public deviceGridService: DeviceGridService,
    private dgExtensionService: DeviceGridExtensionService
  ) {
    this.serverSideDataCallback = this.onDataSourceModifier.bind(this);
  }

  ngOnInit() {
    this.setActionControls();
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  trackByName(_index, column: Column): string {
    return column.name;
  }

  async onDataSourceModifier(
    dataSourceModifier: DataSourceModifier
  ): Promise<ServerSideDataResult> {
    let serverSideDataResult: ServerSideDataResult;
    let filteredSize: number;
    let size: number;
    if (this.childDeviceGrid) {
      const { res, data, paging } = await this.deviceGridService.getChildDeviceData(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery,
        this.withChildren,
        this.parentDeviceId
      );

      filteredSize = await this.deviceGridService.getCountChildDevices(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery,
        this.parentDeviceId
      );
      size = await this.deviceGridService.getTotalChildDevices(this.baseQuery, this.parentDeviceId);

      serverSideDataResult = {
        res,
        data,
        paging,
        filteredSize,
        size
      };
    } else {
      const { res, data, paging } = await this.deviceGridService.getData(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery,
        this.withChildren,
        dataSourceModifier.searchText
      );

      filteredSize = await this.deviceGridService.getCount(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.baseQuery,
        dataSourceModifier.searchText
      );
      size = await this.deviceGridService.getTotal(this.baseQuery);

      serverSideDataResult = {
        res,
        data,
        paging,
        filteredSize,
        size
      };
    }

    this.onColumnsChange.emit(dataSourceModifier.columns);
    this.onDeviceQueryStringChange.emit(
      this.deviceGridService.getDeviceQueryString(dataSourceModifier.columns, this.baseQuery)
    );

    if (this.dataCallback) {
      serverSideDataResult = this.dataCallback(serverSideDataResult);
    }

    return serverSideDataResult;
  }

  setActionControls() {
    const asArrayOfGroupedActionHooks = (hooks: DeviceGridActionHook[]) =>
      Object.values(groupBy(hooks, 'type')) as DeviceGridActionHook[];

    this.dgExtensionService.items$
      .pipe(
        map(asArrayOfGroupedActionHooks),
        switchMap(from),
        map((hooks: DeviceGridActionHook[]) => {
          const { type, icon, text, showOnHover } = hooks[0];
          const matchingHooks = (device: Row) =>
            hooks.filter(hook => hook.deviceMatches(device as IManagedObject));

          const hasMatchingHooks = (device: Row) => !!matchingHooks(device).length;
          const useInventoryDelete = (device: Row) =>
            !hasMatchingHooks(device) && type === BuiltInActionType.Delete;

          const resolveAction = (device: Row) =>
            useInventoryDelete(device)
              ? this.deviceGridService.delete(device as IManagedObject)
              : matchingHooks(device)[0].onAction(device as IManagedObject);

          this.actionControls.push({
            type,
            icon,
            text,
            showOnHover,
            showIf: (device: Row) => type === BuiltInActionType.Delete || hasMatchingHooks(device),
            callback: (device: Row) =>
              toObservable(resolveAction(device))
                .pipe(
                  catchError(_err => EMPTY),
                  takeUntil(this.destroyed$)
                )
                .subscribe(_success => {
                  if (useInventoryDelete(device) || matchingHooks(device)[0].refreshAfterActionDone)
                    this.refresh.emit();
                })
          });
        }),
        takeUntil(this.destroyed$)
      )
      .subscribe();
  }

  updateFiltering(
    columnNames: string[],
    action: {
      type: FilteringActionType;
      payload?: { filteringModifier: FilteringModifier };
    }
  ) {
    const { type } = action;
    if (type === FilteringActionType.ResetFilter) {
      this.dataGrid.clearFilters();
    } else {
      this.dataGrid.updateFiltering(columnNames, action);
    }
  }
}
