import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { IManagedObject } from '@c8y/client';
import {
  ExtensionFactory,
  ExtensionPointWithoutStateForPlugins,
  fromTriggerOnce,
  GenericHookType,
  hookGeneric,
  PluginsResolveService,
  HookOptions,
  BuiltInActionType
} from '@c8y/ngx-components';
import { flatten } from 'lodash-es';
import { Observable } from 'rxjs';
import { shareReplay } from 'rxjs/operators';
import { DeviceGridActionHook } from './device-grid.model';
import { DeviceGridService } from './device-grid.service';

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
export type DeviceGridActionExtension =
  | DeviceGridActionHook
  | DeviceGridActionHook[]
  | ExtensionFactory<DeviceGridActionHook>;

/**
 * A hook to use for Multi Provider extension.
 * @deprecated Consider using the `hookDeviceGridAction` function instead.
 */
export const HOOK_DEVICE_GRID_ACTION = new InjectionToken<DeviceGridActionExtension>(
  'DeviceGridActionHook'
);

/**
 * You can either provide a single `DeviceGridActionExtension` as parameter:
 * ```typescript
 *  hookDeviceGridAction(...)
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  hookDeviceGridAction([...])
 * ```
 *
 * Or you provide an Service that implements `ExtensionFactory<DeviceGridActionExtension>`
 * ```typescript
 *  export class MyDeviceGridActionFactory implements ExtensionFactory<DeviceGridActionExtension> {...}
 *  ...
 *  hookDeviceGridAction(MyDeviceGridActionFactory)
 * ```
 * A typed alternative to `HOOK_DEVICE_GRID_ACTION_DELETE`.
 * @param action The `DeviceGridDeleteExtension`'s or `ExtensionFactory` to be provided.
 * @returns An `Provider` to be provided in your module.
 */
export function hookDeviceGridAction(
  action: GenericHookType<DeviceGridActionExtension>,
  options?: Partial<HookOptions>
) {
  return hookGeneric<DeviceGridActionExtension>(action, HOOK_DEVICE_GRID_ACTION, options);
}

/**
 * A service which defines device grid action options.
 */
@Injectable({
  providedIn: 'root'
})
export class DeviceGridExtensionService extends ExtensionPointWithoutStateForPlugins<DeviceGridActionHook> {
  constructor(
    rootInjector: Injector,
    plugins: PluginsResolveService,
    private router: Router,
    private deviceGridService: DeviceGridService
  ) {
    super(rootInjector, plugins);
    this.items$ = this.setupItemsObservable();
  }

  protected setupItemsObservable(): Observable<DeviceGridActionHook[]> {
    return fromTriggerOnce<DeviceGridActionHook>(this.router, this.refresh$, [
      () =>
        flatten(
          this.injectors.map(injector =>
            injector.get(
              HOOK_DEVICE_GRID_ACTION,
              [
                {
                  type: BuiltInActionType.Delete,
                  deviceMatches: (_mo: IManagedObject) => true,
                  onAction: (mo: IManagedObject) => this.deviceGridService.delete(mo),
                  refreshAfterActionDone: true
                }
              ],
              { self: true }
            )
          )
        ),
      () => this.factories
    ]).pipe(shareReplay(1));
  }
}
